import React, { useState } from 'react'
import DatePicker from "react-datepicker";
import { connect } from "react-redux";
import {SET_USERID,SET_USERNAME,SET_USERMONTHOFFEES,SET_USERFEERECEIVED} from '../Student/constant'

import "react-datepicker/dist/react-datepicker.css";

 function getformatdate(date,props){
    console.log(date);
    const monthNames = ["JAN", "FEB", "MAR", "APR", "MAY", "JUN",
  "JUL", "AUG", "SEP", "OCT", "NOV", "DEC"
];

    if(props.showmonthyear === true){
        return monthNames[date.getMonth()]+"-"+date.getFullYear();
    }
    else{
        return ("0" + date.getDate()).slice(-2)+"-"+ (monthNames[date.getMonth()]) +'-'+date.getFullYear();
    }
 }
 function Datepicker(props) {
    const [startDate, setStartDate] = useState(new Date()); 
    if(props.showmonthyear === true){
        props.SET_USERMONTHOFFEES(getformatdate(startDate,props));
    }
    else{
        props.SET_USERFEERECEIVED(getformatdate(startDate,props));
    }
    const handleDateChange = (e)=> { 
        setStartDate(e);
        if(props.showmonthyear === true){
            props.SET_USERMONTHOFFEES(getformatdate(e,props));
        }
        else{
            props.SET_USERFEERECEIVED(getformatdate(e,props));
        }
    }   
  return (
    <div>
       <DatePicker className='form-control' showMonthYearPicker={props.showmonthyear} selected={startDate}
        onChange={handleDateChange}  dateFormat={props.dateformat} />
    </div>
  )
}
function mapStateToProps(state) {
    return {
        userfeeData: state.RootReducer.RegisterFeeReducer.userfeeData,
    };
}

function mapDispatchToProps(dispatch) {
    return {
        SET_USERID: data => dispatch({ type: SET_USERID, data: data }),
        SET_USERNAME : data =>  dispatch({type: SET_USERNAME ,data:data}),
        SET_USERMONTHOFFEES : data =>  dispatch({type: SET_USERMONTHOFFEES ,data:data}),
        SET_USERFEERECEIVED : data =>  dispatch({type: SET_USERFEERECEIVED ,data:data})
    };
}


export default connect(
    mapStateToProps,
    mapDispatchToProps
)(Datepicker);
 
