import './App.css';
import RegisterFee from './Components/Student/RegisterFee';
import './CSS/form.css'
import { configureStore  } from '@reduxjs/toolkit'
import { Provider } from 'react-redux';
import RootReducer from './Components/RootReducer';
const loadState = () => {
  try {
      let serializedState = localStorage.getItem("reduxState") || initializeState();
      return JSON.parse(serializedState);
  }
  catch (err) {
      return this.initializeState();
  }
}

const initializeState = () => {
  return { }
}


function App() {
  
 const store = configureStore({
  reducer:{
    RootReducer
  },
  loadState
})

  return (
    <Provider store={store}>
        <RegisterFee/>
        </Provider>
    );
}

export default App;
