import React from 'react'
import { default as ReactSelect } from "react-select";
import { connect } from "react-redux";
import {SET_USERID,SET_USERNAME,SET_USERMONTHOFFEES,SET_USERFEERECEIVED} from '../Student/constant'

 function Dropdown(props) {
    
  return (
    <div>
        <ReactSelect
          options={props.options} onChange={props.SetStudent}
        />
    </div>
  )
}

function mapStateToProps(state) {
  return {
      userfeeData: state.RootReducer.RegisterFeeReducer.userfeeData,
      Username :state.RootReducer.RegisterFeeReducer.Username,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    SET_USERID: data => dispatch({ type: SET_USERID, data: data }),
    SET_USERNAME : data =>  dispatch({type: SET_USERNAME ,data:data}),
    SET_USERMONTHOFFEES : data =>  dispatch({type: SET_USERMONTHOFFEES ,data:data}),
    SET_USERFEERECEIVED : data =>  dispatch({type: SET_USERFEERECEIVED ,data:data})
  };
}


export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Dropdown);

