import {SET_USERID,SET_USERNAME,SET_USERMONTHOFFEES,SET_USERFEERECEIVED,SET_FEEAMOUNT} from '../Student/constant'
const initialState = {
    SET_USERID: 0,
    SET_USERNAME :"",
    SET_USERMONTHOFFEES :"",
    SET_USERFEERECEIVED:"",
    SET_FEEAMOUNT:0
}

export default function RegisterFeeReducer(state = initialState, action) {
    switch (action.type) {
        case SET_USERID:
            return { ...state, SET_USERID: action.data }
            case SET_USERNAME:
                return { ...state, SET_USERNAME: action.data }
                case SET_USERMONTHOFFEES:
                return { ...state, SET_USERMONTHOFFEES: action.data }
                case SET_USERFEERECEIVED:
                return { ...state, SET_USERFEERECEIVED: action.data }
                case SET_FEEAMOUNT:
                    return { ...state, SET_FEEAMOUNT: action.data }

        default:
            return state
    }
};