import React from 'react'

export default function Table(props) {
    console.log('table executed');
    console.log(props);
  return (
    <div className="App">
    <table>
        <thead>
      <tr>
        <th>Student Name</th>
        <th>Month of Fees</th>
        <th>Fee Amount</th>
        <th>Received Date</th>
      </tr>
      </thead>
      <tbody>
      {props.data.map((val, key) => {
        return (
          <tr key={key}>
            <td>{val.username}</td>
            <td>{val.feemonth}</td>
            <td>{val.feeamount}</td>
            <td>{val.receiveddate}</td>
          </tr>
        )
      })}
      </tbody>

    </table>
    </div>
  )
}
